@import url(https://fonts.googleapis.com/css?family=Work+Sans&display=swap);
.pokecard {
  /* width: 200px; */
  padding: 8px;
  margin: 1rem; 
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  text-align: center;
  background-color: white;
  border-radius: 3px;
} 

.pokecard-data {
  font-size: 80%;
}

.pokecard-image {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 150px;
  background-color: #eceff1;
  border-radius: 3px;
  margin-bottom: 0.8rem;
}

.pokecard-title {
  margin: 0.4rem auto;
  font-weight: bold;
  font-size: 1.2rem;
}

.pokecard img {
  width: 60%;
  transition: all ease 0.3s;
}

.pokecard img:hover {
   transform: scale(1.2);
   opacity: 0.8; 
}
.pokedex {
  text-align: center;
}

.pokedex-exp {
  margin: 5px 0;
}

.pokedex-cards {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.pokedex-winner {
  margin: 8px 0;
  color: #4caf50;

}

.pokedex-loser {
  margin: 8px 0;
  color: #e91e63;
}
body {
  margin: 0;
  padding: 0;
  background-color: #eceff1;
  color: #4d4d4d;
  font-family: Work Sans;
}

